/**
 * 转换旧数据为新投保数据
 */
module.exports = (function() {
    /**
     * 转换驼峰命名法
     * @param {Object} value
     */
    function camelCase(value) {
        var result = value.replace(/\d/gm, '').replace(/[-|_](\w)/g, function($0, $1) {
            return $1.toUpperCase();
        }).replace(/[-|_]/gm, '');
        return (!value || value === '' || value.length == 0) ? value : result.substring(0, 1).toLowerCase() + result.substring(1);
    }

    function convertObj(option) {
        delete option.short_name;
        return option;
    }

    /**
     * 转换数据
     * @param {*} obj 
     */
    function convertPolicy(obj) {
        obj = typeof obj === 'string' ? eval("(" + obj + ")") : obj;
        let policy = {};
        for (let item in obj) {
            if (item !== 'productId') {
                let _item = {};
                obj[item].forEach(function(option) {
                    option = convertObj(option);
                    let _code = option.subId || camelCase(option.code);
                    if (option.option) { //处理附加险
                        let _opt = {};
                        option.option.forEach(function(opt) {
                            if (opt.code.substring(0, 1) !== '_') {
                                let _optPr = _code.split('_')[0],
                                    _optPrTe = opt.code.split('_')[0];
                                _code = _optPr !== _optPrTe ? _code.replace(_optPr, _optPrTe) : _code;
                                _opt[camelCase(opt.code)] = convertObj(opt);
                            }
                        });
                        option.option = _opt;
                    }
                    _item[_code] = option;
                });
                policy[item] = _item;
            } else {
                policy[item] = obj[item];
            }
        }
        return policy;
    }
    return {
        convertPolicy: convertPolicy
    }
})();


